.cart {
    @include flex(flex-start, flex-start);

    @include mobile {
        flex-wrap: wrap;
    }

    &__info {
        width       : 40%;
        margin-right: 30px;
        margin      : 30px;
        box-shadow  : $box-shadow;
        position    : sticky;
        top         : calc(#{$header-shrink-height}+20px);

        @include mobile {
            width        : 100%;
            margin-right : 0;
            margin-bottom: 30px;
            position     : unset;

        }

        &__txt {
            &>* {
                margin-bottom: 10px;
            }

            p {
                font-size: 1.25rem;
            }

            &__price {
                @include flex(center, space-between);
                padding  : 10px 0;
                font-size: 1.25rem;

                span:nth-child(2) {
                    font-size  : 2rem;
                    color      : $main-color;
                    font-weight: 600;
                }
            }
        }

        &__btn>* {
            margin-bottom: 20px;
        }
    }

    &__list {
        flex-grow: 1;
    }

    &__item {
        @include flex(center, flex-start);
        margin-bottom: 20ox;

        &__image {
            margin-right: 20px;

            img {
                height: 150px;
            }
        }

        &__info {
            @include flex(center, space-between);
            flex-grow: 1;

            @include mobile {
                @include flex(flex-start, center);
                flex-direction: column;

                &>* {
                    margin-bottom: 10px;
                }
            }


            &__name,
            &__price {
                width: 40%;

                @include mobile {
                    width: 100%;
                }
            }

            &__del {
                font-size: 1.5rem;

                i {
                    cursor: pointer;

                    &:hover {
                        color: $main-color
                    }
                }
            }
        }
    }
}