.product {
    @include flex(flex-start, flex-start);

    @include mobile {
        flex-direction: column;
    }

    &__images {
        @include flex(flex-start, flex-start);

        width    : 50%;
        flex-wrap: wrap;


        @include tablet {
            width: 80%;
        }

        @include mobile {
            width: 100%;
        }

        &__list {
            width: 20%;

            &__item {
                cursor: pointer;
            }
        }

        &__main {
            flex-grow  : 1;
            padding-top: 100%;
            position   : relative;

            img {
                position : absolute;
                top      : 0;
                left     : 50%;
                height   : 100%;
                transform: translateX(-50%);
            }
        }

    }

    &__info {
        flex-grow  : 1;
        position   : sticky;
        top        : $header-shrink-height;
        padding-top: 2rem;

        &__title {
            font-size  : 2.5rem;
            font-weight: normal;
        }

        &__item {
            // @include flex(flex-start, flex-start);
            margin-top: 2rem;

            &__price {
                color      : $main-color;
                font-size  : 2rem;
                font-weight: 600;
            }

            &__title {
                font-size    : 1.5rem;
                font-weight  : 600;
                margin-bottom: 1rem;
            }

            &__list {
                @include flex(center, flex-start);

                &__item {
                    @include flex(center, center);

                    width        : 50px;
                    height       : 50px;
                    border-radius: 50%;
                    border       : 2px solid $txt-second-color;
                    cursor       : pointer;
                    margin-right : 1rem;

                    @include mobile {
                        width : 40px;
                        height: 40px;
                    }

                    &.active {
                        border-color: $main-color
                    }

                    &:hover {
                        border-color: $main-color;

                    }

                    .circle {
                        width        : 40px;
                        height       : 40px;
                        border-radius: 50%;

                        @include mobile {
                            width : 30px;
                            height: 30px;
                        }
                    }

                    &__size {
                        font-size     : 1.5rem;
                        text-transform: uppercase;
                    }
                }
            }

            &__quantity {
                @include flex(flex-start, flex-start);

                &__btn {
                    @include flex(center, center);
                    width    : 30px;
                    height   : 30px;
                    border   : 2px solid $txt-second-color;
                    font-size: 1.5rem;
                    cursor   : pointer;

                }

                &__input {
                    @include flex(center, center);
                    height       : 30px;
                    width        : 90px;
                    font-size    : 1.5rem;
                    border-top   : 2px solid $txt-second-color;
                    border-bottom: 2px solid $txt-second-color;
                }
            }
        }
    }
}

.product-description {
    width   : 100%;
    padding : 2rem 2rem 3rem;
    height  : 400px;
    overflow: hidden;
    position: relative;

    @include mobile {
        display: none;
    }

    &.expand {
        height: max-content;
    }

    &__title {
        font-size  : 1.5rem;
        font-weight: 600;
        margin     : 2rem 0;
    }

    &__content {
        font-size  : 1.25rem;
        line-height: 1.875rem;
        text-align : justify;
    }

    &__toggle {
        position : absolute;
        bottom   : 0;
        left     : 50%;
        transform: translateX(-50%);
    }
}