@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');


* {
    padding                    : 0;
    margin                     : 0;
    box-sizing                 : border-box;
    -webkit-tap-highlight-color: transparent;
    user-select                : none;
}

html {
    font-size: 13px;
}

body {
    font-family: "Roboto", sans-serif;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
    color          : unset;
}

a:hover {
    color: $main-color;
}

img {
    max-width: 100%;
}

@each $color,
$val in $colors {
    .color-#{$color} {
        color: $val;
    }

    .bg-#{$color} {
        background-color: $val;
    }

}

@each $val in $number {
    .gird-col-#{$val} {
        grid-template-columns: repeat($val, 1fr);
    }
}

@each $val in $number {
    .gird-col-md-#{$val} {
        @include tablet {
            grid-template-columns: repeat($val, 1fr);
        }
    }
}

@each $val in $number {
    .gird-col-sm-#{$val} {
        @include mobile {
            grid-template-columns: repeat($val, 1fr);
        }
    }
}

.container {
    width    : 100%;
    max-width: 1620px;
    padding  : 0 50px;
    margin   : auto;

    @include tablet {
        padding: 0 20px;
    }

    @include mobile {
        padding: 0 10px;
    }
}



.main {
    margin-top   : $header-height;
    margin-bottom: 2rem;

    min-height: 100vh;

    @include tablet {
        margin-top: calc(#{$header-tablet-height}+20px);
    }

    @include mobile {
        margin-top: calc(#{$header-mobile-height}+10px);
    }
}

.grid {
    display: grid;
}