$main-bg   : #fff;
$main-color: #4267b2;

$txt-main-color  : #000;
$txt-second-color:#8d8d8d;
$txt-white       : #fff;

$btn-main-bg   : $main-color;
$btn-main-color: #fff;

$header-height       : 170px;
$header-tablet-height:70px;
$header-mobile-height: 40px;
$header-shrink-height: 70px;

$box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

$colors: ("blue": #4267b2,
    "orange":#fbb86b,
    "pink": #fe7e73,
    "white": #fff,
    "black": #000,
    "main": $main-color,
    "red":#ff0000,
    "green":#00FF00,
    "purple":#FF00FF,
    "brown":#D2691E,
    "yellow":#FFD700);

$mobile-width: 600px;
$tablet-width:1024px;

$number: (0, 1, 2, 3, 4, 5, 6, 7, 8);